import React, { useState, useEffect } from "react";
import "./styles.css";
import ReactPlayer from "react-player";
import useSockets from "../useSockets";
const url = [
    "https://firebasestorage.googleapis.com/v0/b/gente-mala.appspot.com/o/es%20mi%20dia%20de%20suerte.mp4?alt=media&token=a27dbac0-6441-4f47-a659-9dfd7383bc4b",
    ,
    "https://firebasestorage.googleapis.com/v0/b/gente-mala.appspot.com/o/videoplayback_2.mp4?alt=media&token=62f9a831-d2ad-436a-9532-d06f8c0409fb",
    ,
    "https://firebasestorage.googleapis.com/v0/b/gente-mala.appspot.com/o/VID_29240629_042008_925.mp4?alt=media&token=29a3dac5-f131-4291-8d22-1e8837d06fac",
    ,
    "https://firebasestorage.googleapis.com/v0/b/gente-mala.appspot.com/o/VID-20210319-WA0546.mp4?alt=media&token=835ebbe5-d7da-48e6-ae73-3918dd562026",
];
export default function Dvd() {
    const { mensaje, gift, audience } = useSockets();

    const [video, setVideo] = useState(true);
    const [countvideo, setCountvideo] = useState(0);

    useEffect(() => {
        setVideo(true);
    }, [gift]);
    const deletevideo = () => {
        setCountvideo(Math.floor(Math.random() * (1 + url.length)));
        console.log("termino el video");
        setVideo(false);
    };
    return (
        <div>
            {video && (
                <ReactPlayer
                    width={"100vw"}
                    height={"100vh"}
                    onEnded={() => deletevideo()}
                    url={[url[countvideo]]}
                    playing
                />
            )}
        </div>
    );
}

